<script setup>
import { onMounted, ref } from 'vue';
import { ElMessage } from 'element-plus';
import { User, Camera, Star, Clock, Location, Lock, Setting, Monitor } from '@element-plus/icons-vue';
import { getLoginUserDetail } from '@/api/api.ts'

const user = ref({
  username: '张三', // 示例用户名
  avatar: '', // 头像 URL
  last_login_at: '2025-03-30 10:00:00', // 上次登录时间
  last_login_ip: '192.168.1.1', // 上次登录 IP
  gatewayKey: '05e379be82a738d8', // 对接 Key
  gatewaySecret: '3ef0fbcbf4f213fa68f58e9f5cf4c49c', // 对接密钥
});

const defaultAvatar = 'https://www.gravatar.com/avatar/?d=mp';
const password = ref('');
const confirmPassword = ref('');
const isPasswordEditable = ref(false);

const togglePasswordEdit = () => {
  isPasswordEditable.value = !isPasswordEditable.value;
  if (!isPasswordEditable.value) {
    password.value = '';
    confirmPassword.value = '';
  } else {
    // 如果点击保存密码
    if (password.value && confirmPassword.value) {
      updatePassword();
    }
  }
};

const cancelPasswordEdit = () => {
  isPasswordEditable.value = false;
  password.value = '';
  confirmPassword.value = '';
};

const changeAvatar = () => {
  ElMessage.info('头像更换功能开发中...');
};

const resetGatewayKey = () => {
  user.value.gatewayKey = '';
  ElMessage.success('对接 Key 已重置');
};

const resetGatewaySecret = () => {
  user.value.gatewaySecret = '';
  ElMessage.success('对接密钥已重置');
};

const updatePassword = () => {
  if (!password.value) {
    ElMessage.error('请输入新密码');
    return;
  }
  if (password.value !== confirmPassword.value) {
    ElMessage.error('两次输入的密码不一致');
    return;
  }
  ElMessage.success('密码修改成功');
  isPasswordEditable.value = false;
  password.value = '';
  confirmPassword.value = '';
  // 在这里添加 API 调用逻辑
};

const fetchUserInfo = async () => {
  try {
    const res = await getLoginUserDetail()
    user.value = res.data || {}
  } catch (error) {
    ElMessage.error('获取用户信息失败')
  }
}

onMounted(() => {
  fetchUserInfo()
})
</script>

